import  vue from 'vue';
import { Notify,Loading,QSpinnerPuff } from 'quasar'


import  { firebaseDb , firebaseAuth } from "../boot/firebase";
import {uid} from 'quasar';

let messageRef;
let businessRef;

const state = {
  userDetails : {},
  users       :{},
  messages    :{},
  businessList: {},
  records: {},
  errors:[]
};


const mutations ={
    addBusiness(state,payload) {
      console.log('get b2')

      vue.set(state.businessList , payload.businessId , payload.businessDetails);
    },
    addRecord(state,payload) {
      vue.set(state.records , payload.recordId , payload.recordDetails);
    },

    setUserDetails(state , payload){
        state.userDetails = payload;
    },

    addUser(state,payload){


      vue.set(state.users , payload.userId , payload.userDetails);
    },
    updateUser(state, payload){
      Object.assign(state.users[payload.userId], payload.userDetails)
    },
    addMessage(state, payload){
      vue.set(state.messages, payload.messageId , payload.messageDetails)
    },
    clearMessages(){
      state.messages ={}
    },
    setErrors(state,payload){
      state.errors.push(payload)
    },
    clearError() {
      state.errors = []
    }
};


const actions ={


    registerUser({},payload){

      firebaseAuth.createUserWithEmailAndPassword(payload.email, payload.password)
        .then(response  => {
          let userId = firebaseAuth.currentUser.uid;
          firebaseDb.ref('users/' + userId ).set({
            name : payload.name,
            email : payload.email,
            password : payload.password,
            online : true,
          })



        })
        .catch(error =>{
          console.log(error)
          dispatch('addErrorMessage', error)
          // return error.message
        })
    },
    loginUser({commit,dispatch},payload){
      console.log('?')
      let errorMessage = ''
      firebaseAuth.signInWithEmailAndPassword(payload.email, payload.password)
        .then(response  => {
          let userId = firebaseAuth.currentUser.uid;


        })
        .catch(error =>{

          dispatch('addErrorMessage', error)

        })


    },
    logoutUser(){
        firebaseAuth.signOut();
    },
    handleAuthChange({commit ,dispatch , state }){


          firebaseAuth.onAuthStateChanged(user => {
            if (user) {
              // User is logged in.
              let userId = firebaseAuth.currentUser.uid
              firebaseDb.ref('users/' + userId).once('value', snapshot => {
                let userDetails = snapshot.val()
                commit('setUserDetails', {
                  name: userDetails.name,
                  email: userDetails.email,
                  userId: userId
                })
              })
              dispatch('firebaseUpdateUser', {
                userId: userId,
                updates: {
                  online: true
                }
              })
              dispatch('firebaseGetBusiness',userId)
              dispatch('clearErrorMessage')
              this.$router.replace('/')



            }else{
          // User is signed out.

          dispatch('firebaseUpdateUser',{
                   userId: state.userDetails.userId,
                   updates:{
                        online :false
                   }
              })
              commit('setUserDetails',{});
              this.$router.replace('/auth')

            }
      });
    },

    firebaseUpdateUser({}, payload){
      if (payload.userId){
        firebaseDb.ref('users/' +payload.userId).update(payload.updates)

      }
    },

    firebaseGetUsers({ commit }, payload){

      console.log('get users')

      firebaseDb.ref('users/').on('child_added', snapshot =>{
        let userDetails = snapshot.val();
        let userId = snapshot.key;
        commit('addUser',{
          userId,
          userDetails
        })
      })

      firebaseDb.ref('users/').on('child_changed', snapshot =>{
        let userDetails = snapshot.val();
        let userId = snapshot.key;
        commit('updateUser',{
          userId,
          userDetails
        })
      })

    },

    firebaseGetMessages({commit,state}, otherUserId){

      let userId = state.userDetails.userId
      messageRef = firebaseDb.ref('chats/' +  userId + '/' + otherUserId)
      messageRef.on('child_added', snapshot =>{


        let messageDetails = snapshot.val()
        let messageId = snapshot.key
        commit('addMessage',{
          messageId,
          messageDetails
        })

      })
    },

    firebaseStopGettingMessages({commit}){

      if (messageRef){
        messageRef.off('child_added')
      }
      commit('clearMessages')
    },

    firebaseSendMessage({commit,state},payload){


      let userId = state.userDetails.userId
      payload.message.created = Date.now()

      messageRef = firebaseDb.ref('chats/' +  userId + '/' + payload.otherUserId).push(payload.message)

      payload.message.from ='them'
      messageRef = firebaseDb.ref('chats/' + payload.otherUserId  + '/' +  userId).push(payload.message)

    },

    firebaseAddBusiness({ commit,state },businessData) {

      let userId = state.userDetails.userId
      businessData.created = Date.now()
      firebaseDb.ref('business_list/' +  userId).push(businessData)

    },

    firebaseGetBusiness({ commit ,state},userId){
      Loading.show({
        spinner:QSpinnerPuff
      })

      firebaseDb.ref('business_list/' +  userId).on('child_added', snapshot =>{
        let businessDetails = snapshot.val()
        let businessId = snapshot.key
        commit('addBusiness',{
          businessId,
          businessDetails
        })

        Loading.hide()

      })


    },

    firebaseAddRecord({ commit , state },recordData) {
      let userId = state.userDetails.userId
      recordData.userId = userId;
      firebaseDb.ref('entry_records/'+recordData.businessId).push(recordData)

        Notify.create({
          type: 'positive',
          color: 'positive',
          timeout: 600,
          position: 'center',
          message: 'Record saved.'
        })



    },
    firebaseGetRecordList({commit,state},payload){

      let userId = state.userDetails.userId
      messageRef = firebaseDb.ref('entry_records'+'/'+payload.businessId).on('child_added', snapshot =>{


        let recordDetails = snapshot.val()
        let recordId = snapshot.key
        commit('addRecord',{
          recordId,
          recordDetails
        })

      })
    },

    addErrorMessage({commit },payload){

      let errorMessage = ''
      if (payload.code=="auth/wrong-password"){

        errorMessage = 'Invalid User login info !';
        commit('setErrors', errorMessage);

      }else if(payload.code=="auth/user-not-found"){

        errorMessage = 'No user found !';
      }else{
        errorMessage = 'An error occurred please check your internet connection!';

      }

      commit('setErrors', errorMessage);
    },
    clearErrorMessage({commit}){
      commit('clearError')
    },


};

const getters ={
  users: (state)=>{
    let usersFiltered = {}
    Object.keys(state.users).forEach( key => {
      if(key != state.userDetails.userId){
        usersFiltered[key] = state.users[key]
      }
    })
    return usersFiltered
  },
  loggedInUser: (state) =>{
    if(state.userDetails){
      return state.userDetails;
    }
    return false
  },
  businessListGetter: (state)=>{
    return state.businessList;
  },
  recordListGetter: (state)=>{
    console.log('ggeetter')
    return state.records;
  },
  getErrors: (state)=>{
    return state.errors
  }
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
}




